body {
  margin: 0;
  padding: 0;
  font-family: "Arial", sans-serif;
  background-color: #111;
  color: #fff;
}

.app {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
}

.header {
  text-align: center;
}

.logo {
  font-size: 32px;
  margin-bottom: 20px;
}

.game {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.game-board {
  border: 3px solid #e50914;
  border-radius: 10px;
  overflow: hidden;
}

.board-row {
  display: flex;
}

.square {
  background: #fff;
  color: #000; /* Default color for X */
  font-size: 36px;
  font-weight: bold;
  line-height: 70px;
  height: 70px;
  width: 70px;
  margin: 1px;
  cursor: pointer;
  transition: background 0.3s ease, color 0.3s ease;
}

.square.O {
  color: #2196f3; /* Blue color for O */
}

.winning-line {
  background-color: #42b72a;
}

.board-row::after {
  clear: both;
  content: "";
  display: table;
}

.board-row {
  overflow: hidden;
}

.square:hover {
  background: #e50914;
  color: #fff;
}

.game-info {
  margin-top: 20px;
  font-size: 24px;
  font-weight: bold;
}

.status {
  margin-bottom: 10px;
}

.reset-btn {
  padding: 10px 20px;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  background: #e50914;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease;
}

.reset-btn:hover {
  background: #ff3c56;
}
.winning-square {
  background-color: #42b72a;
  color: #fff;
}
.player-form {
  text-align: center;
  margin-bottom: 20px;
}

input[type="text"] {
  padding: 10px;
  margin: 5px;
  border: 1px solid #ccc;
  border-radius: 5px;
  font-size: 16px;
}

button[type="submit"] {
  padding: 10px 20px;
  font-size: 20px;
  border: none;
  border-radius: 5px;
  background: #e50914;
  color: #fff;
  cursor: pointer;
  transition: background 0.3s ease;
}

button[type="submit"]:hover {
  background: #ff3c56;
}